function Pagination(props){

    const currentPageNumber = props.currentPageNumber;
    const totalPages = props.totalPages;

    console.log(currentPageNumber)

    const style = {
        padding : "1px 10px",
        margin : "10px",
        border : "1px solid gray",
        borderRadius : "5px",
    }

    return(
        <div className="d-flex justify-content-center align-items-center">
            
            {currentPageNumber > 0 ? <span  style={style} onClick={() => props.change(currentPageNumber-1)}>previous</span> : null}
            <span>{currentPageNumber}</span>
            {currentPageNumber < totalPages ? <span style={style}  onClick={() => props.change(currentPageNumber+1)}>next</span> : null}
        </div>
    )

}

export default Pagination