
import { useState , useEffect } from  'react'
import classes from './allBanks.module.css';
import Dropdown from '../dropdown/dropdown'
import Loader from '../Loader/loader'
import {useHistory} from 'react-router-dom'
import Pagination from '../Pagination/pagination'
const cities = [{name:"mumbai",value:"mumbai"},{name : "delhi",value:"delhi"}]
const categories = [{name :"ifsc",vaue:"ifsc"},{name : "branch",value:"branch"},{name : "bank name" , value :"bank_name"},{name : "address",value:"address"}]

function AllBanks(props){

    const [ banks , setBanks ] = useState([])
    const [filteredBanks , setFilteredBanks] = useState([])
    const [currentPageBanks,setCurrentPageBanks] = useState([])
    const [loading,setLoading] = useState(true)
    const [searchCity , setSearchCity] = useState("mumbai")
    const [searchCategory , setSearchCategory] = useState("")
    const [queryInput , setQueryInput] = useState('');
    const [pageNumber,setPageNumber] = useState(0);

    const [pagePerEntries,setPagePerEntries] = useState(10); 
    const [totalPages,setTotalPages] = useState()
   
    const history = useHistory();


    const searchCityHandler = (city) => {
        setSearchCity(city)
       
    }


    const searchCategoryHandler = (category) => {
        setSearchCategory(category)
       
    }


    useEffect(() => {

        setLoading(true)
    

        let data = localStorage.getItem("data");
        if(data){

            data  = JSON.parse(data);
            console.log(data.expiry , Date.now())
            if(data.expiry > Date.now()){
            setBanks(data.banks);
            setFilteredBanks(data.banks);
            setLoading(false)
            return;
            }

            localStorage.clear()
        }


       
        fetch('https://vast-shore-74260.herokuapp.com/banks?city=MUMBAI')
        .then(res => {
            return res.json()
        })
        .then((banks) => {
            console.log(banks)
            setBanks(banks)
            setFilteredBanks(banks)
            setLoading(false)
            

            console.log("????????????")

            const expiytTime = Date.now() + 1000*60*60*24*3;

         
            const localStorageData = {
                banks : banks,
                expiry :  expiytTime 
                
            }
            
            localStorage.setItem('data',JSON.stringify(localStorageData))
        }).catch(err => {
            console.log("something went wrong")
            setLoading(false)
        })

    },[])

    useEffect(() => {
        const requiredBanks = []
        
        console.log(filteredBanks)
        for(let i = pageNumber*pagePerEntries ; i < pageNumber*pagePerEntries +  pagePerEntries && i < filteredBanks.length  ; i++){
            requiredBanks.push(filteredBanks[i])
        }
        console.log(requiredBanks)
        setCurrentPageBanks(requiredBanks);
    },[filteredBanks,pageNumber])


    const searchHandler2 = () => {
        let newFilteredBanks = []
        if(!searchCategory || !queryInput){
            newFilteredBanks = banks.filter(bank => {
                return bank.city.toUpperCase() == searchCity.toUpperCase()
            })
        }else{
            newFilteredBanks = banks.filter(bank => {
                return ((bank.city.toUpperCase() == searchCity.toUpperCase()) && (bank[searchCategory].toUpperCase() == queryInput.toUpperCase()))
            })
        }

        setFilteredBanks(newFilteredBanks)


    }

  



    const fullDetailsHandler = (ifsc) => {
        history.push(`/bank-details/${ifsc}`)
    }

 

    
    useEffect(() => {

        let timer = setTimeout(() => {
            searchHandler2();
        },500)

        return (() => {
            clearTimeout(timer);
        })

    },[queryInput , searchCategory , searchCity,banks])
    


    const handlePageChange = (pageNumber) => {
        setPageNumber(pageNumber)
    }


    useEffect(() => {
        setTotalPages((filteredBanks.length / pagePerEntries) - 1);
    },[filteredBanks])


    console.log(currentPageBanks)
    return(
        <div className="container_fluid">
            <div className={classes.header}>
                <h4>All banks</h4>
                <ul className={classes.dropdown}>
                    <li>
                        <Dropdown  change={searchCityHandler} label="choose city" options={cities}/>
                    </li>
                    <li>
                        <Dropdown change={searchCategoryHandler} label="choose Category" options={categories}/>
                    </li>
                    <li>
                        <input onInput={(event) => setQueryInput(event.target.value)} placeholder="search"></input>
                    </li>

                </ul>
            </div>
            <div>

                   
                <table className={classes.table}>
                    <thead>
                        <tr>
                        <th>Branch Name</th>
                        <th>IFSC</th>
                        <th>NAME</th>
                        <th>ID</th>
                        <th>address</th>
                        </tr>
                    </thead>
                   

                    <tbody>
                       
                        {currentPageBanks.map((bank,id) => {
                            return(
                                <tr key={id} onClick={fullDetailsHandler.bind(this,bank.ifsc)}>
                                    <td>{bank.bank_name}</td>
                                    <td>{bank.ifsc}</td>
                                    <td>{bank.branch}</td>
                                    <td>{bank.bank_id}</td>
                                    <td>{bank.address}</td>
                                </tr>

                            )
                        })}
                    </tbody>
                </table>
                {loading ? <div className="text-center"><Loader/> </div>: null}
                 <Pagination
                            currentPageNumber={pageNumber}
                            totalPages={totalPages}
                            change={handlePageChange}
                        /> 
          
            </div>
           
        </div>
       
    )
}

export default AllBanks