
import {useEffect , useState } from 'react'
import { useHistory } from 'react-router-dom'
import Loader from '../Loader/loader'

function BankDetails(props){

    const [allBanks, setAllBanks] = useState([])
    const [selectedBankIfsc ,setSelectedBankIfsc] = useState();
    const [selectedBank,setSelectedBank] = useState({});
    const [loading,setLoading] = useState(false)
    
    const history = useHistory()


    useEffect(() => {
        let data = localStorage.getItem('data');
        if(data){
         
            data = JSON.parse(data);
            if(data.expiry > Date.now()){
                setAllBanks(data.banks)
                return;
            }else  localStorage.clear();

           
        }
     
        
            setLoading(true)
            fetch("https://vast-shore-74260.herokuapp.com/banks?city=MUMBAI")
            .then(res => {
                return res.json();
            })
            .then(res => {
                const localStorageData = {
                    expiry : Date.now() + 1000*60*60*24*3,
                    banks : res
                }

                localStorage.setItem('data',JSON.stringify(localStorageData));
                setAllBanks(res);
                setLoading(false)
            })
            .catch((err) => {
                setLoading(false)
                console.log("something went wrong")
            })
        }   
    ,[])

    useEffect(() => {
        const temp = history.location.pathname.split('/')
        const selectedBankIfsc = temp[temp.length - 1];
        console.log(selectedBankIfsc);
        setSelectedBankIfsc(selectedBankIfsc);
    },[history,allBanks])

    useEffect(() => {
        console.log(allBanks)
        const selectedBank = allBanks.find((bank) => {
            return bank.ifsc === selectedBankIfsc
        })

        
        if(selectedBank) setSelectedBank(selectedBank);
        console.log(selectedBank)
    },[selectedBankIfsc,allBanks])

   
    return(
        <div className="container_fluid">
            <div className="row">
                <div className="col-12">
                    <h3>Selected Bank details</h3>
                </div>

            </div>
            <div className="row">
                {loading ? <div className="text-center"><Loader /></div> : null}
                {Object.keys(selectedBank).length == 0 && !loading ? <h2>NO BANK FOUND</h2>: null}
                {Object.keys(selectedBank).map(ele => {
                    return(
                        <>
                        <div className="col-6">
                            {ele}
                        </div>
                        <div className="col-6">
                            {selectedBank[ele]}
                        </div>
                        </>
                    )
                })}
            </div>
            
        </div>
    )
}

export default BankDetails